import React, { useEffect, useState } from 'react';
import Modal from 'react-modal';
import Swal from 'sweetalert2';


import DateTimePicker from 'react-datetime-picker';
import moment from 'moment';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { uiCloseModal } from '../../actions/ui';
import { clearActiveEvent, eventAddNew, eventStartAddNew, eventStartUpdate, updateActiveEvent } from '../../actions/events';


const customStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)',
     
    }
  };

Modal.setAppElement('#root')

const now = moment().minutes(0).seconds(0).add(1);

const initEvent = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: now.add(1, 'hours').toDate()
}

export const CalendarModal = () => {
    
    const dispatch = useDispatch();

    const [dateStart, setDateStart] = useState(now.toDate())
    const { modalOpen: modalState } = useSelector( state => state.ui );
    const { activeEvent } = useSelector( state => state.calendar );
    
    const [dateEnd, setDateEnd] = useState(moment(dateStart).add(1, 'hours').toDate())
    const [titleValid, setTitleValid] = useState(true);
    
    const [formValues, setFormValues] = useState(initEvent);
    const { title, notes, start, end } = formValues;


    useEffect(() => {
        
        if( activeEvent ){

            setFormValues(activeEvent)

        }else{

            setFormValues(initEvent)
        }

    }, [activeEvent,setFormValues])

    
    const handleInputChange = ({ target }) =>{

        setFormValues({
            ...formValues,
            [target.name]: target.value


        })
    }

    const closeModal = ()=>{
        dispatch(uiCloseModal())
        setFormValues( initEvent )
        dispatch(clearActiveEvent())
    }

    const handleStartDateChange = (e)=>{
        setDateStart(e)
        setDateEnd(moment(e).add(1,'hours').toDate())
        setFormValues({
            ...formValues,
            start: e,
            end: moment(e).add(1,'hours').toDate()

        })
    }

    const handleEndDateChange = (e)=>{
        setDateEnd(e)
        setFormValues({
            ...formValues,
            end: e
        })
     }

     const handleSubmitForm = (e)=>{

        e.preventDefault();
        const momentStart = moment ( start );
        const momentEnd = moment ( end );

        if(momentStart.isSameOrAfter( momentEnd )){
            console.log("fecha 2 debe ser mayor")
            Swal.fire('Error!','La fecha final debe ser menor que la fecha inicial.','error')
            return;
        }

        if(title.trim().length < 2){
            setTitleValid(false)
            return; //setTitleValid(false);
        }

        if ( activeEvent ){

            dispatch(eventStartUpdate(formValues));

        }else{
        dispatch(eventStartAddNew(formValues))
    }

        setTitleValid(true)
        closeModal();
}

    return (
       <>
 <Modal
          isOpen={ modalState }

          //onAfterOpen={afterOpenModal}
          onRequestClose={ closeModal }
          style={ customStyles }
          closeTimeoutMS={ 200 }
          className="modal"
          overlayClassName="modal-fondo"
        >

<h1> { (activeEvent) ? 'Editar Evento' : 'Nuevo evento' } </h1>
<hr />
<form 
    className="container"
    onSubmit= { handleSubmitForm }
    >

    <div className="form-group">
        <label>Fecha y hora inicio</label>
        <DateTimePicker
        onChange={ handleStartDateChange }
        value={ dateStart }
        className="form-control"
      />
    </div>

    <div className="form-group">
        <label>Fecha y hora fin</label>
        <DateTimePicker
        onChange={ handleEndDateChange }
        value={ dateEnd }
        minDate= { dateStart }
        className="form-control"
      />
    </div>

    <hr />
    <div className="form-group">
        <label>Titulo y notas</label>
        <input 
            type="text" 
            className={ `form-control ${ !titleValid && 'is-invalid' }`  }
            name="title"
            value= { title }
            onChange = { handleInputChange }
            autoComplete="off"
        />
        <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
    </div>

    <div className="form-group">
        <textarea 
            type="text" 
            className="form-control"
            placeholder="Notas"
            rows="5"
            name="notes"
            value={ notes }
            onChange = { handleInputChange }
        ></textarea>
        <small id="emailHelp" className="form-text text-muted">Información adicional</small>
    </div>

    <button
        type="submit"
        className="btn btn-outline-primary btn-block"
        
    >
        <i className="far fa-save"></i>
        <span> Guardar</span>
    </button>

</form>

</Modal>
       </>
    )
}
