import Swal from "sweetalert2";
import { fechtConjwt } from "../helpers/fecht";
import { prepareEvents } from "../helpers/prepareEvents";
import { types } from "../types/types";

export const eventStartLoading = ()=>{

    return async(dispatch)=>{

        try {
            const res = await fechtConjwt('events/getEvents')
            const body = await res.json();

            
            if(body.ok){

                const events = prepareEvents( body.eventos );
                dispatch(eventLoaded(events))

            }

        } catch (error) {
            console.log(error)
        }


    }

}

const eventLoaded = (events)=>{
    return{
        type: types.eventLoaded,
        payload: events
    }
}

const eventAddNew = (event)=>({
    type: types.eventAddNew,
    payload: event
})

export const eventLogOut = ()=>({
    type:types.eventLogOut
})

export const eventStartAddNew = (event)=>{
    return async( dispatch, getState )=>{
        
        const {uid, name} = getState().auth;

        try {
        const resp = await fechtConjwt('events/newEvent', event, 'POST');
        const body = await resp.json();
        console.log(body.evento)
            if(body.ok){

                event.id = body.evento.id;
                event.user = {
                    _id: uid,
                    name: name
                }
                dispatch(eventAddNew(event));
            }

        

        } catch (error) {
            console.log(error);            
        }
        
    }
}


export const eventSetActive = (event)=>({
    type: types.eventSetActive,
    payload: event
})

export const clearActiveEvent = ()=>({
    type: types.clearActiveEvent,
    
})

export const eventStartUpdate = ( event )=>{
    return async(dispatch)=>{

        try {

            console.log(event)
            const resp = await fechtConjwt(`events/updateEvent/${event.id}`,event,'PUT');
            const body = await resp.json();
 
            if(body.ok){
                dispatch(updateActiveEvent(event))
            }else{
                console.log(body)
                Swal.fire('Error',body.mgs,'error');
            }
        } catch (error) {
            console.log(error)
        }


    }
}


export const updateActiveEvent = (event)=>({
    type: types.updateActiveEvent,
    payload: event
    
})


export const startDeleteEvent = ()=>{

    return async(dispatch, getState)=>{

        try {

            const { id } = getState().calendar.activeEvent;
            const resp = await fechtConjwt(`events/deleteEvent/${id}`,{},'DELETE');
            const body = await resp.json();
 
            if(body.ok){
                dispatch(deleteActiveEvent())
            }else{
                console.log(body)
                Swal.fire('Error',body.mgs,'error');
            }
        } catch (error) {
            console.log(error)
        }
 

    }

}

export const deleteActiveEvent = ()=>({
    type: types.deleteActiveEvent,
  
    
})