import React, { useEffect, useState } from 'react';
import {  Calendar, momentLocalizer} from 'react-big-calendar';
import moment from 'moment';
import 'moment/locale/es'

import { messages } from '../../helpers/calendar-messages-es';
import { CalendarEvent } from './CalendarEvent';
import { CalendarModal } from './CalendarModal';
import { useDispatch, useSelector } from 'react-redux';
import { uiOpenModal } from '../../actions/ui';
import { eventSetActive, clearActiveEvent, eventStartLoading } from '../../actions/events';
import { AddNewFab } from '../ui/AddNewFab';
import { DeleteEventFab } from '../ui/DeleteEventFab';

moment.locale('es');

const localizer = momentLocalizer(moment); // or globalizeLocalizer

/*const myEventsList = [{
    title:'Primer Evento',
    start: moment().toDate(),
    end: moment().add(2,'hours').toDate(),
    bgcolor: '#bbb',
    notes: 'Esta es una nota del evento',
    user: {
        _id: '1235',
        name: 'Leandro Cordero'
    }
}];*/

export const CalendarScreen = () => {

const dispatch = useDispatch();


useEffect(() => {
    dispatch(eventStartLoading());
}, [dispatch])

const {events:myEventsList, activeEvent} = useSelector( state => state.calendar );

const { uid } = useSelector( state => state.auth );

const [lastView, setLastView] = useState(localStorage.getItem('lastView') || 'month');


const onDoubleClick = (e)=>{
    
   dispatch(uiOpenModal())
}

const onViewChange = (e)=>{
    setLastView(e);
    localStorage.setItem('lastView', e);
    
}


const onSelectEvent = (e)=>{
   dispatch(eventSetActive(e))
    
  }

const onSelectSlot = (e)=>{

    if(e.action==='doubleClick'){

        console.log(e)
        dispatch(uiOpenModal(e))

    }else{

        dispatch(clearActiveEvent())
    }

    
}

  

const eventStyleGeters = (event, start, end, isSelected)=>{

    const style = {
        backgroundColor: ( uid === event.user._id) ? '#367CF7' : '#465660' ,
        borderRadius: '0px',
        opacity: 0.8,
        display: 'block',
        color: '#FFF'
    }

    
 return {style: style}

}
    



/*const styleSelected = {

        backgroundColor: '#66bafa',
        borderRadius: '0px',
        border: '2px solid blue',
        opacity: 0.8,
        display: 'block',
        position: 'absolute',
        color: '#FFF',
        transition: 'opacity .2s ease-in-out',
        width: '100%'

    }
*/
    
    return (
        <div  className="calendar__calendar-screen">

    <Calendar
      localizer={localizer}
      events={myEventsList}
      startAccessor="start"
      endAccessor="end"
      messages= { messages }
      onSelectSlot={ onSelectSlot }
      selectable = {true}
      onDoubleClickEvent= { onDoubleClick }
      onSelectEvent= { onSelectEvent }
      onView= { onViewChange }
      view= { lastView }
      eventPropGetter = { eventStyleGeters }
      components={{
          event: CalendarEvent
      }}
    />
         <CalendarModal />
         <AddNewFab />

         { (activeEvent) &&  <DeleteEventFab />}
        
    
    </div>
    )
}
