import React from 'react'
import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';
import { startRegister } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';

export const RegisterScreen = () => {

    const [ formValues, handleInputChange] = useForm( {name: '', email: '', password: '', password1: ''} );
    const dispatch = useDispatch();
    const { name, email, password, password1 } = formValues;

     const handleRegister = (e)=>{
        e.preventDefault();
        
        if(password !== password1){

           return Swal.fire('Error','Las contraseñas deben ser iguales','error');
        }
        dispatch(startRegister(name, email,password));
        }
     


    return (

        <div className="container auth__login-container">
            <div className="auth__login-form-1">
                <h3>Crear Cuenta</h3>
                <form onSubmit= { handleRegister }>
                <div className="form-group">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Nombre"
                    name="name"
                    value={ name }
                    onChange={ handleInputChange }
                />
            </div>
            <div className="form-group">
                <input
                    type="email"
                    className="form-control"
                    placeholder="Correo"
                    name="email"
                    value={ email }
                    onChange={ handleInputChange }
                />
            </div>
            <div className="form-group">
                <input
                    type="password"
                    className="form-control"
                    placeholder="Contraseña"
                    name="password"
                    value={ password } 
                    onChange={ handleInputChange }
                />
            </div>

            <div className="form-group">
                <input
                    type="password"
                    className="form-control"
                    placeholder="Repita la contraseña"
                    name="password1"
                    value={ password1 } 
                    onChange={ handleInputChange }
                />
            </div>

            <div className="form-group">
                <input 
                    type="submit" 
                    className="auth__btnSubmit" 
                    value="Crear cuenta" />
            </div>
                </form>
            </div>
    </div>
        
    

    )
}
