import moment from 'moment';
import { types } from "../types/types";

/*{
    id: new Date().getTime(),
    title:'Primer Evento',
    start: moment().toDate(),
    end: moment().add(2,'hours').toDate(),
    bgcolor: '#bbb',
    notes: 'Esta es una nota del evento',
    user: {
        _id: '1235',
        name: 'Leandro Cordero'
    }}*/

const initialState = {

    events:[],
    activeEvent: null

}

export const calendaarReducer = (state = initialState, action) =>{

    switch ( action.type ) {
        case types.eventSetActive:
            
            state.events[0] = {...state.events[0], bgcolor:'green'}

            return{...state,
                    activeEvent: action.payload
                
            };

        case types.eventAddNew:
            
        return{...state,
        events: [...state.events,
            action.payload],
        }

        case types.clearActiveEvent:
            
            return{...state,
            activeEvent: null
            }

        case types.updateActiveEvent:
           
            return{...state,
            events: state.events.map(e=> (e.id === action.payload.id) ? action.payload : e)
            }

        case types.deleteActiveEvent:
           
        return{...state,
        events: state.events.filter(e=> (e.id !== state.activeEvent.id)),
        activeEvent: null
        }
    
        case types.eventLoaded:
            return{...state,
            events: [...action.payload]
        }

        case types.eventLogOut:
            return{

              ...initialState
            }

        default:
            return state;
    }



}