import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom'
import { startGoogleLogin, startLogin } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';

export const LoginScreen = () => {

    const [ formValues, handleInputChange] = useForm( {email: '', password: ""} );
    const dispatch = useDispatch();
    const { email, password } = formValues;

     const handleLogin = (e)=>{
        e.preventDefault();
        dispatch(startLogin(email,password));
        }
     
const { checking } = useSelector( state => state.auth );

const handleGoogleLogin = ()=>{

    dispatch(startGoogleLogin())

};

    return (
        <div className="container auth__login-container">
            <div className="auth__login-form-1">
                <h3>Ingreso</h3>
                <form onSubmit={ handleLogin }>
                    <div className="form-group">
                        <input 
                            type="text"
                            className="form-control"
                            placeholder="Correo"
                            name="email"
                            value={ email }
                            onChange={ handleInputChange }
                        />
                    </div>
                    <div className="form-group">
                        <input
                            type="password"
                            className="form-control"
                            placeholder="Contraseña"
                            autoComplete= "off"
                            name="password"
                            value= { password }
                            onChange= { handleInputChange }
                        />
                    </div>
                    <div className="form-group">
                        <button 
                            type="submit"
                            className="auth__btnSubmit"
                            >{!!checking ? (<><span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span> Loading...</>
                            ) : ('Login' )}</button>
                    </div>

                    <div className="auth__social-networks">
             
              <p>Login with Social</p>
          
              <div className="google-btn"
              onClick = { handleGoogleLogin }>
                <div className="google-icon-wrapper">
                    <img className="google-icon" src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg" alt="google button" />
                </div>
                <p className="btn-text">
                    <b>Sign in with google</b>
                </p>
              </div>
            
          </div>

                    <div className="form-group">
                    <Link className="" to="/auth/register">Crear Cuenta</Link>
                    </div>
                </form>
            </div>
    </div>
   
   
   
   )
}
