import React from 'react'
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { startLogOut } from '../../actions/auth';




export const UiOptions = (props) => {
    const dispatch = useDispatch();

  const handleLogOut = ()=>{
    dispatch(startLogOut())
  }


    const { userName, photoURL } = props;
    
    return (
<div className="navsettings__container">

    <ul className="navsettings__span navbar-nav">
         <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="/" id="navbarDarkDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          {userName}
          </a>
            <ul className="dropdown-menu dropdown-menu-dark" aria-labelledby="navbarDarkDropdownMenuLink">
                <li><a className="dropdown-item" href="/">
                    <span className="">Notificaciones  <i className="far fa-bell" /></span>
                    <span className="badge rounded-pill bg-secondary">5</span>
                    </a>
                    
                </li>
                <li><a className="dropdown-item" href="/">Another action</a></li>
                <li onClick={ handleLogOut }>
                    <Link className="dropdown-item" to="/auth/login">
                            Logout <i className="fas fa-sign-in-alt" />
                    </Link>
                </li>
            </ul>
        </li>

        <li className="nav-item dropdown">
          <a className="nav-link " href="/" id="navbarDarkDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          
                 
          
          </a>
            <ul className="dropdown-menu dropdown-menu-dark" aria-labelledby="navbarDarkDropdownMenuLink">
                <li><a className="dropdown-item" href="/"></a></li>
                <li><a className="dropdown-item" href="/"></a></li>
            </ul>
        </li>

    </ul>   
        

        <img className="navsettings__userImg ml-3" src={ photoURL? photoURL : '/assets/userPlaceholder.png' } alt="userfoto"></img>
</div>
    )
}
