import Swal from 'sweetalert2';
import { fechtConjwt, fechtSinjwt } from '../helpers/fecht';
import { types } from '../types/types';
import { firebase, googleAuthProvider } from '../firebase/firebase-config';
import { eventLogOut } from './events';

export const startLogin = (email, password)=>{
    
    return async( dispatch )=>{

        const resp = await fechtSinjwt('auth',{ email, password },'POST');
        const body = await resp.json();

            if(body.ok){
            localStorage.setItem('token',body.token);
            localStorage.setItem('token-init-date',new Date().getTime());

            dispatch(login({
                uid: body.uid,
                name: body.name,
                photoURL: body.photoURL
            }))
           
        }else{
            Swal.fire('Error', body.msg, 'error')
        }


    }


}

const login = (user)=>({

type: types.authLogin,
payload: user

})

export const startRegister = (name, email, password)=>{
    return async(dispatch)=>{

        const resp = await fechtSinjwt('auth/new',{ name, email, password },'POST');
        const body = await resp.json();

        if(body.ok){
            localStorage.setItem('token',body.token);
            localStorage.setItem('token-init-date',new Date().getTime());

            dispatch(login({
                uid: body.uid,
                name: body.name,
                photoURL: body.photoURL
            }))
           
        }else{
            Swal.fire('Error', body.msg, 'error')
        }
    }
}


export const startChecking = ()=>{

    return async(dispatch)=>{

        const resp = await fechtConjwt('auth/renew');
        const body = await resp.json();
       
        
        if(body.ok){
            localStorage.setItem('token',body.token);
            localStorage.setItem('token-init-date',new Date().getTime());

            dispatch(login({
                uid: body.uid,
                name: body.name,
                photoURL: body.photoURL
                
            }))
           
        }else{
            //Swal.fire('Error', body.msg, 'error')
            dispatch(checkingFinish())
        }

    }
}

const checkingFinish = ()=>({type: types.finishChecking})


export const startLogOut = ()=>{

return (dispatch)=>{
    localStorage.clear();
    dispatch(eventLogOut());
    dispatch(logout());
}

}

const logout = ()=>({type: types.authLogout})


export const startGoogleLogin = ()=>{

    return async( dispatch ) =>{

         const { user } = await firebase.auth().signInWithPopup( googleAuthProvider );
         const { displayName, email, photoURL, uid } = user;
         
         const resp = await fechtSinjwt('auth/socialLogin',{ name:displayName, email, photoURL, uid },'POST');
         const body = await resp.json();
 
         if(body.ok){
             localStorage.setItem('token',body.token);
             localStorage.setItem('token-init-date',new Date().getTime());
 
             dispatch(login({
                 uid: body.uid,
                 name: body.name,
                 photoURL: body.photoURL
             }))
            
         }else{
             Swal.fire('Error', body.msg, 'error')
         } 
    
    }
}

